// Imports
import React from "react";

// Interfaces
export interface CardContent {
    imageUrl: string,
    heading: string,
    buttonLabel: string,
    buttonLink: string
}

// Element
function CardSingle(props: CardContent) {

    // Render the Element
    return (

        <article className="home-cards__item">

            { /* Image */ }
            <img
                className="home-cards__item-image"
                src={ props.imageUrl }
                alt={ props.heading }
                loading="lazy"
            />

            { /* Content */ }
            <div className="home-cards__item-content">

                { /* Heading */ }
                <h3 className="home-cards__item-heading"> { props.heading } </h3>

                { /* Button */ }
                <a
                    className="home-cards__item-button"
                    href={ props.buttonLink }
                    title={ props.buttonLabel }
                >
                    
                    { /* Label */ }
                    { props.buttonLabel }

                    { /* Icon */ }
                    <svg xmlns="http://www.w3.org/2000/svg" width="9.699" height="6.065" viewBox="0 0 9.699 6.065">
                        <path d="M9.521,99.386a.607.607,0,0,0,0-.858L7.1,96.1a.607.607,0,1,0-.858.858l1.391,1.391H.606a.606.606,0,1,0,0,1.212H7.629l-1.391,1.391a.607.607,0,0,0,.858.858l2.425-2.425Z" transform="translate(0 -95.925)" fill="#2688c8"/>
                    </svg>
                
                </a>

            </div>

        </article>

    );

}

// Export the Element
export default CardSingle;