// Imports
import React from "react";

// Interfaces
interface CompanyLogoContent {
    name: string,
    link: string,
    imageUrl: string
}

// Element
function CompanyLogo(props: CompanyLogoContent) {

    // Render the Element
    return (

        <a
            className="home-trusted__logo"
            href={ props.link }
            title={ props.name }
        >

            { /* Image */ }
            <img loading="lazy" src={ props.imageUrl } alt={ props.name } />

        </a>

    );

}

// Export the Element
export default CompanyLogo;