// Imports
import React, { useEffect, useRef } from "react";
import Button from "./components/Button";
import Feature from "./components/Feature";
import Hero from "./components/Hero";
import TrustedBy from "./components/TrustedBy";
import Scrollbar from 'smooth-scrollbar';

// Element
function App() {

    const CONTAINER_ELEMENT = useRef(null);

    useEffect(() => {

        if (CONTAINER_ELEMENT.current === null) return;

        // Setup the Smooth Scroll
        Scrollbar.init(CONTAINER_ELEMENT.current, {
            damping: 0.1
        });

    }, []);

    // Render the Element
    return (

        <div
            className="page-scroll"
            ref={ CONTAINER_ELEMENT }
        >

            { /* Hero */ }
            <Hero
                heading="Search extensions, themes and plugins for your favorites Editors"
                description="A wide range of beautiful addons for your IDE. Find the best, download and integrate them in your IDE with a click."
            />

            { /* Trusted */ }
            <TrustedBy
                heading="Trusted by many companies"
            />

            { /* Features */ }
            <main className="home-features">

                { /* Feature */ }
                <Feature
                    heading="Intelligent code autocompletion built-in with React"
                    description="Take advantage of the latest React features to easily build and deploy your apps within seconds."
                    imageUrl="images/home/code-feature-1.png"
                />

                { /* Feature */ }
                <Feature
                    heading="Declare modules efficiently and without any information loss"
                    description="We feature and implement the latest technologies in order for your NPM packages to be loaded efficiently in no-time."
                    imageUrl="images/home/code-feature-2.png"
                />

                { /* Feature */ }
                <Feature
                    heading="Easier handle forms and capture visitors' data from inputs"
                    description="Thanks to our intelligent Forms handler, we guarantee building Forms will be straightforward and easy to work with."
                    imageUrl="images/home/code-feature-3.png"
                />

            </main>

            { /* Buttons */ }
            <section className="home-buttons">

                { /* Download */ }
                <Button
                    type="primary"
                    label="Download the Editor"
                    link="/"
                    target="_blank"
                >

                    { /* Icon */ }
                    <svg xmlns="http://www.w3.org/2000/svg" width="13.653" height="13.654" viewBox="0 0 13.653 13.654">
                        <path d="M8.533,0a.853.853,0,0,0,0,1.707h2.205L5.371,7.077A.854.854,0,0,0,6.579,8.285l5.368-5.371V5.12a.853.853,0,0,0,1.707,0V.853A.852.852,0,0,0,12.8,0Zm-6.4.853A2.133,2.133,0,0,0,0,2.987V11.52a2.133,2.133,0,0,0,2.133,2.133h8.533A2.133,2.133,0,0,0,12.8,11.52V8.533a.853.853,0,0,0-1.707,0V11.52a.428.428,0,0,1-.427.427H2.133a.428.428,0,0,1-.427-.427V2.987a.428.428,0,0,1,.427-.427H5.12a.853.853,0,1,0,0-1.707Z" fill="#1a0a1b"/>
                    </svg>

                </Button>

                { /* Docs */ }
                <Button
                    type="secondary"
                    label="Read the Docs"
                    link="/"
                    target="_blank"
                >

                    { /* Icon */ }
                    <svg xmlns="http://www.w3.org/2000/svg" width="13.653" height="13.654" viewBox="0 0 13.653 13.654">
                        <path d="M8.533,0a.853.853,0,0,0,0,1.707h2.205L5.371,7.077A.854.854,0,0,0,6.579,8.285l5.368-5.371V5.12a.853.853,0,0,0,1.707,0V.853A.852.852,0,0,0,12.8,0Zm-6.4.853A2.133,2.133,0,0,0,0,2.987V11.52a2.133,2.133,0,0,0,2.133,2.133h8.533A2.133,2.133,0,0,0,12.8,11.52V8.533a.853.853,0,0,0-1.707,0V11.52a.428.428,0,0,1-.427.427H2.133a.428.428,0,0,1-.427-.427V2.987a.428.428,0,0,1,.427-.427H5.12a.853.853,0,1,0,0-1.707Z" fill="#888"/>
                    </svg>

                </Button>

            </section>

        </div>

    );

}

// Export the Element
export default App;