// Imports
import React from "react";

// Interfaces
export interface FeatureContent {
    imageUrl: string,
    heading: string,
    description: string
}

// Element
function Feature(props: FeatureContent) {

    // Render the Element
    return (

        <article className="home-features__item" data-aos="fade-up">

            { /* Image */ }
            <img
                className="home-features__item-image"
                src={ props.imageUrl }
                alt={ props.heading }
                loading="lazy"
            />

            { /* Content */ }
            <div className="home-features__item-content">

                { /* Heading */ }
                <h2 className="home-features__item-heading"> { props.heading } </h2>

                { /* Description */ }
                <p className="home-features__item-description"> { props.description } </p>

            </div>

        </article>

    );

}

// Export the Element
export default Feature;