// Imports
import React from "react";

// Interfaces
interface MouseContent {
    link?: string
}

// Element
function MouseIcon(props: MouseContent) {

    // Render the Element
    return (

        <a
            href={ props.link }
            className="home-hero__mouse"
            title="Scroll down"
        >

            { /* Circle */ }
            <div className="home-hero__mouse-circle"></div>

        </a>

    );

}

// Export the Element
export default MouseIcon;