// Imports
import React from "react";

// Interfaces
interface ButtonContent {
    type: 'primary' | 'secondary',
    label: string,
    link: string,
    children?: any,
    target?: '_blank' | '_self'
}

// Element
function Button(props: ButtonContent) {

    // Render the Element
    return (

        <a
            href={ props.link }
            className={`button button-${ props.type }`}
            title={ props.label }
            target={ props.target ? props.target : '_self' }
        >

            { /* Label */ }
            { props.label }

            { /* Icon */ }
            { props.children }

        </a>

    );

}

// Export the Element
export default Button;