// Imports
import React, { useRef, useEffect } from "react";
import CompanyLogo from "./CompanyLogo";
import { tns } from 'tiny-slider';

// Interfaces
interface TrustedByContent {
    heading: string
}

// Element
function TrustedBy(props: TrustedByContent) {

    const SLIDER_GRID = useRef(null);
    
    useEffect(() => {

        if (SLIDER_GRID.current === null) return;

        // Setup the Slider
        tns({
            container: SLIDER_GRID.current,
            items: 3,
            startIndex: 0,
            fixedWidth: 150,
            gutter: 60,
            slideBy: 1,
            rewind: true,
            loop: true,
            controls: false,
            nav: false,
            center: true,
            autoplay: false,
            autoplayButtonOutput: false,
            preventActionWhenRunning: true,
            mouseDrag: true,

            responsive: {

                1024: {
                    disable: true
                }

            }

        });

    }, []);

    // Render the Element
    return (

        <section className="home-trusted">

            { /* Heading */ }
            <h2 className="home-trusted__heading"> { props.heading } </h2>

            { /* Grid */ }
            <div
                className="home-trusted__grid"
                ref={ SLIDER_GRID }
            >

                { /* Logo */ }
                <CompanyLogo
                    name="Google"
                    link="https://www.google.com/"
                    imageUrl="images/logos/logo-google.png"
                />

                { /* Logo */ }
                <CompanyLogo
                    name="Netflix"
                    link="https://www.netflix.com/"
                    imageUrl="images/logos/logo-netflix.png"
                />

                { /* Logo */ }
                <CompanyLogo
                    name="Amazon"
                    link="https://www.amazon.com/"
                    imageUrl="images/logos/logo-amazon.png"
                />

            </div>

        </section>

    );

}

// Export the Element
export default TrustedBy;