// Imports
import React from "react";
import Card from "./Card";
import MouseIcon from "./MouseIcon";

// Interfaces
interface HeroContent {
    heading: string,
    description: string
}

// Element
function Hero(props: HeroContent) {

    // Render the Element
    return (

        <section className="home-hero">

            { /* Container */ }
            <article className="home-hero__container">

                { /* Heading */ }
                <h1 className="home-hero__heading"> { props.heading } </h1>

                { /* Description */ }
                <p className="home-hero__description"> { props.description } </p>

                { /* Mouse */ }
                <MouseIcon
                    link="#products"
                />

            </article>

            { /* Cards */ }
            <div className="home-cards" id="products">

                { /* Card */ }
                <Card
                    heading="Plugins, add-ons and extensions for WebStorm"
                    imageUrl="images/home/cards-image-1.png"
                    buttonLabel="Download"
                    buttonLink="/"
                />

                { /* Card */ }
                <Card
                    heading="Icon packs, themes and add-ons for Sublime"
                    imageUrl="images/home/cards-image-2.png"
                    buttonLabel="Download"
                    buttonLink="/"
                />

                { /* Card */ }
                <Card
                    heading="Extensions, themes and more for VS Code"
                    imageUrl="images/home/cards-image-3.png"
                    buttonLabel="Download"
                    buttonLink="/"
                />
            
            </div>

        </section>

    );

}

// Export the Element
export default Hero;